:root{
    --color-dark:#27292a;
    --color-darkest:#1f2122;
    --color-light:#fff;
    --color-primary:#ec6090;
    --color-icons:#ffff00;
    --color-bg-body:var(--color-darkest);
    --distance-primary:50px;
    --padding-primary:var(--distance-primary);
    --margin-primary:var(--distance-primary);
    --border-reduis-primary:calc(var(--distance-primary)/2);
    --font-weight-bold:900;
    --font-weight-normal:400;
    --font-family-primary:'Poppins', sans-serif;


}
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;900&family=Roboto:ital,wght@0,100;1,100;1,400&display=swap');

body{
    background-color: var(--color-bg-body);
    font-family:var( --font-family-primary)
}


ul{
    list-style-type: none;
}

a{
    text-decoration: none;
}